.search-bar {
  display: flex;
}

.search-bar__input {
  box-sizing: border-box;
  color: var(--font-color);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  flex-grow: 1;
  padding: 0;
  font-family: Avenir, serif;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar__input--size--big {
  text-indent: 62px;
  background-position: 26px, 0;
  background-size: 19px;
  height: 70px;
  font-size: 16px;
}

.search-bar__input--size--small {
  text-indent: 33px;
  background-position: 13px, 0;
  background-size: 11px;
  height: 42px;
  font-size: 14px;
}

.search-bar__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input:focus {
  text-shadow: 0 4px 4px #00000040;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff, #f6f6f7);
  background-repeat: no-repeat, repeat;
  border-radius: 3px;
  outline: none;
  font-weight: 900;
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input::-webkit-input-placeholder {
  color: var(--font-color);
}

.search-bar__input:-moz-placeholder {
  color: var(--font-color);
  opacity: 1;
}

.search-bar__input::-moz-placeholder {
  color: var(--font-color);
  opacity: 1;
}

.search-bar__input:-ms-input-placeholder {
  color: var(--font-color);
}

.search-bar__input::placeholder {
  color: var(--font-color);
}

.main__item {
  margin-top: 20px;
}

